$main-bg-color: #202020;
$sidebar-bg-color: #212121;
$bg-hline-color: #353535;
$bg-border-color: #363636;
$bg-searchicon-color: #6f6f6f;
$bg-searchbarbtn-color: #313131;
$bg-input: #121212;
$bg-category-color: #373737;
$bg-maincontent-color: #181818;

@import "normalize.scss";

body {
  background-color: $main-bg-color;
  font-family: Roboto, Noto, sans-serif;
  overflow-x: hidden;
}

.container {
  max-width: 100%;

  .header {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    position: relative;
    margin-top: 10px;

    .icon {
      margin-left: 20px;
    }
    .icon:hover {
      cursor: pointer;
    }

    .menu {
      display: flex;
      flex-direction: row;
      align-items: center;

      .logo {
        margin-left: 30px;
      }
    }

    .searchbar {
      display: flex;
      flex-direction: row;
      align-items: center;

      #searchbar-item {
        background-color: $bg-input;
        border: 0.5px solid $bg-searchicon-color;
        -webkit-appearance: none;
        -webkit-font-smoothing: antialiased;
        width: 500px;
        box-shadow: none;
        color: #fff;
        font-family: "Roboto", "Noto", sans-serif;
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;
        margin-left: 4px;
        max-width: 100%;
        padding-left: 10px;
      }
      .searchbar-button {
        width: 65px;
        border: 1px solid $bg-searchbarbtn-color;
        background-color: $bg-searchbarbtn-color;
        border-radius: 0 2px 2px 0;
        margin: 0;
      }
      .user-account__avatar {
        background: red;
      }
      .logo {
        position: absolute;
        left: 10px;
      }
      .microphone-search {
        margin-left: 15px;
      }

      .user-account {
        margin-right: 20px;
      }
      .user-account__avatar {
        stroke: #0f1c3f;
        stroke-width: 0.05;
      }
    }
  }
}

.youtube-more {
  width: 24px;
  height: 24px;
}

.microphone-search__btn {
  height: 20px;
  width: 20px;
}

.grid-icons {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;

  .notification {
    margin-right: 30px;
    margin-left: 40px;
  }
  .upload-button {
    margin-right: 40px;
  }
}

img {
  cursor: pointer;
}

.content {
  display: flex;
  flex-direction: row;
  .content-sidebar {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
  }
  .content-sidebar__menu {
    background-color: $sidebar-bg-color;
    height: 100%;
    margin-top: 30px;
    color: #fff;

    .content-sidebar__item {
      cursor: pointer;
    }
  }
}
.yt-icon {
  color: #909090;
}

.youtube-logo {
  width: 97px;
  height: 20px;
}

.youtube-title {
  font-weight: 400;
  line-height: 14px;
  font-size: 10px;
}

.main-content {
  background: $bg-maincontent-color;
  height: 1000px;
  width: 100%;
  margin-top: 10px;
  .main-content::before {
    width: 100%;
    height: 1px;
    display: block;
    content: "";
    background: #fff;
  }
  .video-grid {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
  }
  .video-card {
    margin-top: 25px;
    margin-right: 15px;
    position: relative;

    .video-card__item {
      height: 180px;
      width: 321px;
      background: #fff;
    }

    .video-time {
      background: black;
      color: #fff;
      width: 50px;
      padding: 0;
      position: absolute;
      bottom: 0;
      right: 0;

      h4 {
        font-size: 13px;
        margin: 0;
      }
    }
  }

  .categories-video {
    background: $sidebar-bg-color;
    padding-top: 20px;
    border-top: 1px solid $bg-border-color;
    border-bottom: 1px solid $bg-border-color;
    color: #fff;
    padding-bottom: 20px;
    display: flex;
    align-items: center;
    flex-direction: row;

    .category-item {
      margin-left: 15px;
      background: $bg-category-color;
      padding: 10px;
      text-align: center;
      border-radius: 16px;
      cursor: pointer;
    }
    .category-item,
    .white-item {
      font-size: 15px;
    }
  }
}

.white-item {
  background: #fff;
  color: black;
  padding: 10px 10px 10px 10px;
  text-align: center;
  border-radius: 60%;
  margin-left: 15px;
  cursor: pointer;
}

